.info {
  display: flex;
  grid-gap: 5%;
  text-align: center;
  margin-right: 7%;
  margin-left: 7%;
  align-items: center;


  &-item {
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
    text-align: center;

    &__description {
      margin: 0;
    }
  }
}

.photo-card {
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 2px 1px -1px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
}
